@import '../../utils/styles/colors.scss';

//Card Article
article{
  display: flex;
  justify-content: center;
  align-items:center;
  flex-direction: column;
  width: 100%;
  height: 800px;
  background-color: $gris-claire;
  border-radius: 10px;
  margin-bottom: 20px;
  z-index: -1;
  @media (max-width: 1080px) {
    width: 95%;
    margin-top: 20px;
   }
}

//Header
h1{
  text-align: center;
  width: 250px;
  color: $blanc;
  font-size: 17px;
  margin: 0px;
}


.rating{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 100px;
  @media (max-width: 1080px) {
   width: 300px;
   }
}

.fa-star{
  color: $rouge;
  font-size: 18px;
}

//Image
article img{
  width: 100%;
  height: 550px;
 
}

//Social
.Container-social{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 90%;
  height: 10px;
  border-top: 1px solid $blanc;
  margin-top: 20px;
  gap: 10px;
}

.Container-social .Items{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 30px;
}

.Container-social .Items svg{
  width: 30px;
  margin: 0px;
}

.Container-social .Items p{
  color: $blanc;
  cursor: pointer;
}

.Container-social .Items p:hover{
  color: $rouge;
}

.fa-share, .fa-comment, .fa-thumbs-up{
  color: $rouge;
}

//Social active
.Container-social-active {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 93%;
  height: 0px;
  margin-top: 20px;
  margin-bottom: 0px;
  margin-left: 20px;
}

.Items-active{
  display: flex;
  width: auto;
  height: 30px;
}

.Items-active p{
  color: $blanc;
  font-size: 10px;
  margin: 10px 20px 0px 0px;
}

.fa-heart{
  font-size: 18px;
  color: $rouge;
  margin-top: 5px;
}