@import '../../utils/styles/colors.scss';

//Search-bar

.Container-search-bar-dashboard{
  display: none;
  @media (max-width: 1080px) {
    display: flex;
    justify-content: center;
    width: 100%;
   }
}

.Container-search-bar {
  justify-content: center;
  position: absolute;
  top: 45%;
  left: 0%;
  right: 0%;
  width: 100%;
  display: flex;
  align-items: center;
  @media (max-width: 1080px) {
    display: none;
   }
}

.Container-search-bar-frendslist {
  justify-content: center;
  position: relative;
  margin: 10px;
  display: flex;
  align-items: center;
  @media (max-width: 1080px) {
    margin-top: 55px;
   }
}

.Search-bar {
  width: 340px;
  height: 50px;
  background: $gris;
  display: flex;
  align-items: center;
  border-radius: 60px;
  padding: 10px;
}

.Search-bar-dashboard{
  display: flex;
  justify-content: center;
  width: 280px;
  height: 50px;
  background: $noir;
  border-radius: 60px;
  padding: 10px;
  margin-bottom: 20px;
}

.Search-bar-frendslist{
  display: flex;
  justify-content: center;
  width: 280px;
  height: 50px;
  background: $gris;
  border-radius: 60px;
  padding: 10px;
  margin-bottom: 20px;
}

.Search-bar input {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  flex: 1;
  border: 0;
  padding: 9px 10px;
  font-size: 20px;
  min-width: 100px;
  color: #cccc;
  @media (max-width: 1080px) {
    width: 100px;
   }
}

.Search-bar-dashboard input {
  background: transparent;
  flex: 1;
  border: 0;
  padding: 9px 10px;
  font-size: 20px;
  min-width: 100px;
  color: #cccc;
  @media (max-width: 1080px) {
    width: 100px;
   }
}


.Search-bar-frendslist input {
  background: transparent;
  flex: 1;
  border: 0;
  padding: 9px 10px;
  font-size: 20px;
  min-width: 100px;
  color: #cccc;
  @media (max-width: 1080px) {
    width: 100px;
   }
}



::placeholder{
  color: $blanc;
}
.Search-bar button, .Search-bar-dashboard button, .Search-bar-frendslist button{
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  border-radius: 50%;
  margin-left: 10px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  color: $blanc;
  background-color: $rouge;
  @media (max-width: 1080px) {
    margin: 0px;
   }
}

.Search-bar button svg, .Search-bar-dashboard button svg, .Search-bar-frendslist button svg  {
  margin: 0px;
}

.Search-bar button:hover, .Search-bar-dashboard button:hover {
  color: $noir;
  background-color: $rouge;
}

