.Container-article{
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 180px;
    width: 420px;
    @media (max-width: 1080px) {
        padding-top: 100px;
        width: 100%;
       }
}

main {
    display: flex;
    width: auto;
    justify-content: space-around;
    @media (max-width: 1080px) {
        flex-direction: column;
       }
    }

    .Frends-list{
        @media (max-width: 1080px) {  
            display: none;  
           }
    }