@import '../../utils/styles/colors.scss';

//Frends-list
.Container-frendslist{
  background: $rouge;
  width: 310px;
  margin-right: 45px;
  display: flex;
  flex-direction: column;
  position: sticky;
  height: 6%;
  margin-left: 20px;
  border-radius: 10px;
  min-width: 210px;
  padding-top: 50px;
  @media (max-width: 1080px) {
    position: fixed;
    top: 6%;
    left: 0%;
    width: 100%;
    height: 100%;
    margin: 0px;
    z-index: -100;
  }
}

.List-amis{
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-y: scroll;
  background-color: $noir;
  width: 93%;
  margin: 10px;
  height: 420px;
  border-radius: 10px;
}

.Profil{
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.Profil img{
  width: 64px;
  height: 64px;
  background: $noir;
  border-radius: 30px;
}

.Profil p {
  margin-left: 20px;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: 0em;
  color: $blanc;
}

::-webkit-scrollbar {
  background: $rouge;
}

::-webkit-scrollbar-thumb{
  background: $noir;
  border: 10px solid $noir;
  border-radius: 10px;
  
}

